.stars-title {
  margin-bottom: 1rem;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 3rem;
  color: #171822;

  @media (min-width: 1024px) {
    font-size: 1.8rem;
  }
}

.stars {
  width: 20rem;
  display: flex;
  justify-content: space-between;
  margin-bottom: 3rem;
}

.star {
  cursor: pointer;

  img {
    width: 2.5rem;
    height: 2.5rem;

    @media (min-width: 1024px) {
      width: 3rem;
      height: 3rem;
    }
  }
}

