.single-content {

  &__text {
    padding: 3rem 0;

    p,
    ul,
    ol {
      margin-bottom: 2rem;
      font-size: 1.6rem;
      font-weight: 400;
      line-height: 3rem;
      color: #171822;

      @media (min-width: 1024px) {
        font-size: 1.8rem;
      }
    }

    ol {
      padding-left: 1.8rem;
    }
    
    ul,
    ol {
      margin-bottom: 2rem;
    }

    ul {
      padding-left: 2rem;

      @media (min-width: 1024px) {
        padding-left: 2.5rem;
      }

      li {
        position: relative;

        &::before {
          position: absolute;
          content: "";
          top: 0.8rem;
          left: -1.9rem;
          transform: rotate(135deg);
          width: 1.2rem;
          height: 0.7rem;
          border-top: 0.2rem solid #f44949;
          border-right: 0.2rem solid #f44949;

          @media (min-width: 1024px) {
            top: 50%;
            left: -1.6rem;
            transform: rotate(135deg) translateY(1rem);
            width: 1.7rem;
          }
        }
      }
    }

    h2,
    h3,
    h4,
    h5,
    h6 {
      margin: 2rem 0;

      @media (min-width: 1024px) {
        margin: 4rem 0;
      }
    }

    h2 {
      font-size: 2.4rem;
      font-weight: 600;
      line-height: 3rem;
      color: #171822;

      @media (min-width: 1024px) {
        font-size: 3rem;
      }
    }

    h3 {
      font-size: 2.2rem;

      @media (min-width: 1024px) {
        font-size: 2.6rem;
      }
    }

    h4 {
      font-size: 2.2rem;

      @media (min-width: 1024px) {
        font-size: 2.4rem;
      }
    }

    h5 {
      font-size: 2rem;

      @media (min-width: 1024px) {
        font-size: 2.2rem;
      }
    }

    h6 {
      font-size: 1.8rem;

      @media (min-width: 1024px) {
        font-size: 2rem;
      }
    }

    .quote {
      position: relative;
      padding-left: 3.2rem;
      margin-bottom: 5rem;

      &::before {
        position: absolute;
        content: "";
        left: 0;
        top: 0;
        width: 0.4rem;
        height: 100%;
        background: #f44949;
      }

      &::after {
        position: absolute;
        content: "";
        right: 0;
        bottom: -3rem;
        width: 3.3rem;
        height: 2.4rem;
        background: url("../img/quote-left.png") 100% 100%/cover no-repeat;

        @media (min-width: 1024px) {
          width: 4.1rem;
          height: 3.2rem;
        }
      }

      p {
        margin-bottom: 0;
        font-size: 1.6rem;
        font-weight: 600;
        font-style: italic;
        line-height: 3rem;
        color: #171822;

        @media (min-width: 1024px) {
          font-size: 1.8rem;
        }
      }
    }

    img {
      width: 100%;
      border-radius: 0.5rem;
      margin: 2rem 0;
    }
  }
}